@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.session-metadata-wrapper {
  max-height: 40px;
  overflow: hidden;
  transition: all 0.65s;

  &.expanded-metadata {
    max-height: 520px;
  }

  .sm-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .dropdown-toggle-wrapper {
    .dropdown-toggle-btn {
      @include circles(28px, pick-color('primary', 'web'));
      border-radius: 4px !important;
      transition: all 0.2s ease;

      .toggle-icon {
        width: 18px;
        height: 18px;
        font-size: 18px;
        transition: all 0.2s ease;
        color: white;
      }

      &.toggled .toggle-icon {
        transform: rotate(-180deg);
      }
    }
  }

  .gps-availability {
    color: pick-color('primary', 'charcoal');
    font-weight: 500;
    line-height: 19px;

    .gps-text {
      font-weight: 400;

      &.available {
        cursor: pointer;
        text-decoration: underline;
        color: pick-color('secondary', 'green');
      }

      &.unavailable {
        cursor: not-allowed;
        pointer-events: none;
        color: color.adjust(pick-color('primary', 'ash'), $lightness: 15%);
      }
    }
  }

  .audit-trail-btn {
    display: flex;
    align-items: center;
    gap: 6px;
    background-color: pick-color('primary', 'blue');
    color: white;
    padding: 6px 16px;
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    user-select: none;
  }

  .metadata-content {
    background-color: white;
    border: 1px solid pick-color('primary', 'fog');
    border-radius: 8px;

    .sm-content-grid {
      display: grid;
      grid-template-columns: 140px 110px 140px 130px 140px;
      grid-template-rows: 1fr 1fr;
      justify-content: space-between;
      gap: 16px;
      row-gap: 32px;
      cursor: default;
      padding: 16px 12px;

      h6 {
        font-size: 14px;
        color: pick-color('primary', 'charcoal');
        line-height: 20px;
      }

      span {
        margin-top: 5px;
        font-size: 13px;
        color: pick-color('primary', 'ash');
      }

      h6,
      span {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .grid-item {
        position: relative;

        .location-popup {
          width: 140px;
          display: none;
          position: absolute;
          top: 100%;
          left: 90%;
          background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 40%);
          border: 1px solid color.adjust(pick-color('primary', 'fog'), $lightness: 8%);
          border-radius: 10px;
          border-top-left-radius: 0;
          padding: 8px;
          z-index: 60;

          div {
            font-size: 14px;
            color: pick-color('primary', 'web');

            &:not(:last-child) {
              margin-bottom: 6px;
            }
          }
        }

        &:hover .location-popup {
          display: block;
        }
      }
    }

    .metadata-form {
      padding: 16px 12px;
      margin-bottom: 8px;

      .form-wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 20px;
        gap: 20px;

        .desc-wrapper {
          grid-row: span 3;
        }

        .cat-wrapper {
          anchor-name: --categoryAnchor;
          // position: relative;
          width: 100%;
        }
      }
    }
  }
}

.cat-outside {
  position: absolute;
  position-anchor: --categoryAnchor;
  left: anchor(left, 0px);
  top: anchor(top, 0px);
  bottom: anchor(bottom, 0px);
  right: anchor(right, 0px);
  transition: all 0.5s;

  &.not-expanded {
    opacity: 0;
    transform: scale(0);
  }
}
